      margin-bottom: 35px; }
    .vacancies__wrapper {
      display: flex; }
    .vacancies__aside {
      width: calc(100% / 3);
      padding-right: 10px; }
    .vacancies__questions {
      position: sticky;
      top: 20px; }
    .vacancies__item {
      margin-bottom: 40px; }
    .vacancies__content {
      width: calc(100% / 3 * 2); } }

@media (min-width: 1200px) {
  .vacancies__aside {
    width: 400px; }
  .vacancies__link.active::before {
    left: -25px; }
  .vacancies__content {
    width: 760px; } }

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  overflow-x: hidden;
  --theme-background-color: #fff;
  --theme-text-color: #000; }
  body.menu-opened, body.modal-opened {
    overflow: hidden; }
  body[theme='dark-theme'] {
    --theme-background-color: #202020;
    --theme-text-color: #fff; }

[data-aos] {
  visibility: hidden; }

[data-aos].aos-animate {
  visibility: visible; }

[data-aos='fade-left-full'] {
  transform: translate3d(100%, 0, 0); }

[data-aos='fade-right-full'] {
  transform: translate3d(-100%, 0, 0); }

.page {
  position: relative; }

[theme='dark-theme'] .page > .lines {
  top: 75px; }

.tippy-popper {
  max-width: 270px; }

.tippy-tooltip {
  padding: 8px 12px;
  color: #000;
  font-size: 12px;
  text-align: center;
  background-color: #fed63f;
  border-radius: 8px; }

.tippy-content {
  padding: 0; }

.tippy-tooltip[data-placement^='bottom'] > .tippy-svg-arrow {
  top: -6px; }

.tippy-svg-arrow {
  position: absolute;
  width: 13px;
  height: 6px; }
  .tippy-svg-arrow svg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
